.single-issue{
  border: 1px solid $mdb-label-color;
  border-radius: 3px;
  margin-bottom: 10px;
  padding: 10px;
  @include shadow-z-1();

  .post-actions{
    border-top: 1px solid #eee;
    margin-top: 10px;
    margin-left: -10px;
    margin-right: -10px;

    .btn-group{
      a{
        color: $brand-success;
        text-decoration: none;
      }

      &.vote-actions{
        margin: 5px -10px 0px;

        a{
          padding: 0px 7px;
          border-right: 1px solid #eee;
        }
      }
    }
  }
}

.feedback_v2{
  border: 1px solid $mdb-label-color;
  border-radius: 3px;
  margin-bottom: 10px;
  padding: 5px 10px;
  //background: rgba(238, 238, 238, 0.3);
  @include shadow-z-1();

  .user-details{
    margin-bottom: 10px;

    .user-img{
      display: inline;
      margin-right: 10px;
      -webkit-transform: rotate(-20deg);
      -moz-transform: rotate(-20deg);
      -ms-transform: rotate(-20deg);
      -o-transform: rotate(-20deg);
      transform: rotate(-20deg);
    }

    .user-name{
      font-size: 16px;
      font-weight: 100;
    }
  }

  .user-feedback{
    height: 42px; // For 2 lines only
  }

  .company-details{
    margin-top: 10px;

    .stars{
      display: inline;
      position: relative;
      top: 3px;
    }

    .more-details{
      float: right;
      margin: 0px;
    }
  }
}

.feedback{
  position: relative;
  border: 1px solid $mdb-label-color;
  border-radius: 3px;
  margin-bottom: 10px;
  padding: 5px 7px;
  
  .user-img {
    img {
      height: 50px;
    }
  }
  
  .user-details{
    h4{
      margin: 0;
    }
  }
  
  .user-feedback{
    
  }

  .feedback-overlay, .action-buttons{
    display: none;
  }

  &:hover{
    .action-buttons{
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;
      width: 100%;
      height: 100%;
      text-align: center;

      .buttons-box{
        position: relative;
        display: block;
        height: 100%;

        .more-details{
          position: absolute;
          left: 50%;
          top: 45%;
          transform: translate(-50%, -50%);
        }
      }

    }

    .feedback-overlay{
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 5;
      background-color: rgba(0, 0, 0, 0.5);
    }
  }
}
ul.stars {
  padding: 0px;

  li {
    display: inline;

    i{
      color: #206820;
    }

    &.total-rating{
      margin-left: 5px;
    }
  }
}
.parsley-error{
  color: $brand-danger;
}
ul.parsley-errors-list {
  padding: 0px;

  li {
    list-style-type: none;
  }
}
.left-margin {
  padding: 0px;
  margin-left: 10px;
}
.star {
  margin-top: 20px;
}
.img-rounded {
  margin-bottom: 20px;
}
.font-stars{
  font-size: 19px;
}
.categories-container{
  h2{
    font-size: 20px;
    font-weight: 400;
  }

  .child-box{
    list-style-type: none;
    padding: 0px;

    a{
      font-size: 16px;
      line-height:30px;
      color: $green-600;

      &:hover{
        color: darken($green-600, 6%);
      }
    }
  }
}
.criterion-container{
  .removeCriterionField{
    position: relative;
    z-index: 1;
  }
}

.jssocials-container{
  font-size: 10px;
}

.loading{
  position: relative;

  .loading-box{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    top: 0;
    left: 0;

    .fa{
      position: absolute;
      transform: translate(-50%, -50%);
      left: 45%;
      top: 30%;
    }
  }
}

#feedbackModal{
  .modal-title{
    font-size: 18px;
    color: #666;
    
    a{
      font-size: 18px;
      color: #666;
      text-decoration: none;
    }
  }

  .popup_feedback{
    label{
      color: #333;
    }

    .user-feedback{
      color: #333;
    }
  }
}

