.custom-input-group-btn{
  padding: 0px !important;

  button{
    padding: 6px 12px;
    margin: 0px !important;
    background-color: transparent;
    box-shadow: none;

    &.grouped-btn{
      border-bottom: 1px solid $input-border;
      height: 30px;
      border-radius: 0px;
    }

    &:hover, &:focus{
      background-color: transparent !important;
    }
  }
  
  .dropdown-menu{
    a{
      cursor: pointer;
    }
  }
}


